@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  font-family: 'Open Sans', sans-serif;
  padding: 0;
  margin: 0; }

select {
  font-family: 'Open Sans', sans-serif; }

.App {
  max-width: 1100px;
  margin: auto; }

.Chart {
  margin: 30px 0 0 0; }

.Nav {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  border-style: solid;
  border-width: 1px 0;
  border-color: #ddd; }
  .Nav__item {
    padding: 10px; }
  .Nav select {
    background: #fff;
    padding: 5px; }

.Bar {
  -webkit-animation: fadeIn 500ms;
          animation: fadeIn 500ms;
  display: -webkit-flex;
  display: flex; }

@-webkit-keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }
  .Bar__time {
    padding: 10px; }
  .Bar__data {
    box-sizing: border-box;
    position: relative;
    padding: 10px;
    -webkit-flex-grow: 1;
            flex-grow: 1; }
  .Bar__block {
    background: cornflowerblue;
    position: absolute;
    top: 1px;
    left: 0;
    height: calc(100% - 4px);
    transition: all ease 500ms;
    z-index: -1; }
  .Bar__average {
    position: absolute;
    width: 1px;
    height: 100%;
    background: red;
    top: 0; }

